import { createArticle } from '@/api/article'

export default {
    middleware: 'auth',
    data() {
        return {
            article: {
                title: '',
                description: '',
                body: '',
                tagList: ''
            }
        }
    },
    methods: {
        async publishArticle() {
            try {
                const article = this.article
                article.tagList = this.article.tagList.split(',')
                const { data } = await createArticle({
                    article
                })
                this.$router.push({
                    name: 'article',
                    params: {
                        slug: data.article.slug
                    }
                })
            } catch (error) {
                console.log(error);
                
            }
        }
    }
}
